import React from 'react';
import './blogdetails.css'
import Banner from "../../component/Banner/Banner";
import {BlogData , Category} from "../../blogData";
import {Link, useParams} from 'react-router-dom'
import PostShare from "./PostShare";
import Comment from "./Comments";
const BlogDetails = ({match}) => {

    const pid = match.params.id;

    const blogData= BlogData;
    const getblogdata = blogData.find(tree => tree.postId == pid );
    const getCatagory = Category.find(cat => cat.catId == getblogdata.catagoryId );

    console.log(getCatagory);

    return (
        <>
            <Banner title={"Blog Details"}/>


            <div id="blog_post_details" className="section">
                <div className="container">
                    <div className="row">
                        <div className="col-md-9 col-lg-8 ">
                            <div className="blog-post">
                                <div className="fetured-img">
                                    <img src={getblogdata.postImage} alt=""
                                         className="img-fluid"/>
                                </div>
                                <div className="post-date">
                                    <p>{getblogdata.publishedAt}</p>
                                </div>

                                <div className="post-title">
                                    <h1>{getblogdata.title}</h1>
                                </div>
                                <div className="post-details">
                                    <span><i className="fas fa-user"></i> Jhone Aderson </span>
                                    |
                                    <span><i className="fas fa-comment"></i>{getblogdata.like} </span>
                                    |<span><i className="fas fa-tags"></i> {getCatagory.cat}</span>
                                </div>

                                <div className="post-content">
                                    <p>
                                        {getblogdata.postDetails}
                                    </p>

                                </div>
                                <div className="st ">
                                    <div className="dd">
                                        <div className="post-tags">
                                            <h4>Tags:</h4>
                                            {getblogdata.postTag.map((tag,index)=>(
                                                <Link to="/" className="post-tag" key={index}>{tag}</Link>
                                            ))}

                                        </div>
                                        <PostShare blogdata={getblogdata}/>
                                    </div>
                                </div>

                                <div className="post-communt">


                                    <div className="comments">
                                        <h3>Leave a Comment</h3>
                                        <p>Your email address will not be published. Required fields are marked*</p>



                                        <Comment postId={getblogdata.postId}/>
                                    </div>
                                </div>


                            </div>
                        </div>
                        <div className="col-md-3 col-lg-4 ">
                            <div className="sidebar">

                                <div className="search-widget">
                                    <form action="#" className="search">
                                        <div className="input-group mb-3">
                                            <input type="text" className="form-control" placeholder=""/>
                                                <div className="input-group-append">
                                                    <button className=" btn-md px-3 border-0" type="submit" id="d"><i
                                                        className="fas fa-search"></i></button>
                                                </div>
                                        </div>
                                    </form>

                                </div>
                                <div className="sidebar-widget">
                                    <div className="popular-post-widget">
                                        <div className="sidebar-widget-title">
                                            <h3>Popular Post</h3>
                                        </div>
                                        <div className="sidebar-widget-contents">
                                            <div className="popular-post-widget-content">
                                                <div className="popular-post-widget-img">
                                                    <img src="./img/blogdetaild/01.jpg" alt=""/>
                                                </div>
                                                <div className="popular-post-widget-details">
                                                    <p className="publish-date"><i className="fas fa-calendar"></i> 17
                                                        Dec, 2019</p>
                                                    <a className="post-title">
                                                        <h4>Tips Digitizing Financial Services dummy now.</h4>
                                                    </a>
                                                </div>
                                            </div>
                                            <div className="popular-post-widget-content">
                                                <div className="popular-post-widget-img">
                                                    <img src="./img/blogdetaild/02.jpg" alt=""/>
                                                </div>
                                                <div className="popular-post-widget-details">
                                                    <p className="publish-date"><i className="fas fa-calendar"></i> 17
                                                        Dec, 2019</p>
                                                    <a className="post-title">
                                                        <h4>Tips Digitizing Financial Services dummy now.</h4>
                                                    </a>
                                                </div>
                                            </div>
                                            <div className="popular-post-widget-content">
                                                <div className="popular-post-widget-img">
                                                    <img src="./img/blogdetaild/03.jpg" alt=""/>
                                                </div>
                                                <div className="popular-post-widget-details">
                                                    <p className="publish-date"><i className="fas fa-calendar"></i> 17
                                                        Dec, 2019</p>
                                                    <a className="post-title">
                                                        <h4>Tips Digitizing Financial Services dummy now.</h4>
                                                    </a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="sidebar-widget">
                                    <div className="category-widget">
                                        <div className="sidebar-widget-title">
                                            <h3>Categories</h3>
                                        </div>
                                        <div className="sidebar-widget-contents">
                                            <div className="categories">
                                                <a href="#!"><i className="fas fa-caret-right"></i> Business Plans </a>
                                                <a href="#!"><i className="fas fa-caret-right"></i> Business Plans </a>
                                                <a href="#!"><i className="fas fa-caret-right"></i> Business Plans </a>
                                                <a href="#!"><i className="fas fa-caret-right"></i> Business Plans </a>
                                                <a href="#!"><i className="fas fa-caret-right"></i> Business Plans </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="sidebar-widget">
                                    <div className="archive-widget">
                                        <div className="sidebar-widget-title">
                                            <h3>Archives</h3>
                                        </div>
                                        <div className="sidebar-widget-contents">
                                            <ul className="Archives">
                                                <li>
                                                    <a href="#!"><i className="fas fa-calendar-alt"></i> January 2018
                                                    </a>
                                                </li>
                                                <li>
                                                    <a href="#!"><i className="fas fa-calendar-alt"></i> February 2018
                                                    </a>
                                                </li>
                                                <li>
                                                    <a href="#!"><i className="fas fa-calendar-alt"></i>March 2018</a>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                                <div className="sidebar-widget">
                                    <div className="contact-widget">
                                        <div className="sidebar-widget-title">
                                            <h3>Quick Contact</h3>
                                        </div>
                                        <div className="sidebar-widget-contents">
                                            <form action="#" className="quick-contact-form">
                                                <div className="form-group">

                                                    <input type="text" name="name" id="name-c" className="form-control"
                                                           placeholder="Name"/>
                                                        <input type="email" name="name" id="email-c"
                                                               className="form-control" placeholder="Email"/>
                                                            <textarea name="massage" id="massage-c"
                                                                      className="form-control" rows="5"></textarea>

                                                            <button className="btn common-button" type="submit">SUBMIT
                                                            </button>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                                <div className="sidebar-widget">
                                    <div className="tag-widget">
                                        <div className="sidebar-widget-title">
                                            <h3>Categories</h3>
                                        </div>
                                        <div className="sidebar-widget-contents">
                                            <div className="tags">

                                                {Category.map(cat=>(
                                                    <Link to="/"> {cat.cat} </Link>
                                                ))}
                                            </div>

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default BlogDetails;
