import React from 'react';
import './contactus.css'
import emailjs from 'emailjs-com';
import Banner from "../../component/Banner/Banner";
const ContactUs = () => {

    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('service_jt8hrfd', 'template_f7wq02n', e.target, 'user_uylyyQRZxhpG33vp71nex')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
        e.target.reset();
    }

    return (
        <>
            <Banner title={"Contact Us"}/>
            <div id="Contact_us" className="section">
                <div className="container">
                    <div className="row ">
                        <div className="col-md-4 align-self-center">
                            <div className="contact-info">
                                <h4>Our Offices</h4>
                                <h2>Contact Info</h2>
                                <p>There are many variations of passages of an Lorem Ipsum available but the at majority
                                    have alteration.</p>
                                <div className="location ">
                                    <div className="info-img">
                                        <i className="fas fa-map-marker-alt"></i>
                                    </div>
                                    <div className="info-text">
                                        <h3>Find Us</h3>
                                        <p>Progoth Soroni, Dhaka 1229, Bangladesh</p>
                                    </div>
                                </div>
                                <div className="phone ">
                                    <div className="info-img">

                                        <i className="fas fa-phone"></i>
                                    </div>
                                    <div className="info-text">
                                        <h3>Call Us</h3>
                                        <p>+880 1631 330094</p>
                                    </div>
                                </div>
                                <div className="mail ">
                                    <div className="info-img">

                                        <i className="fas fa-envelope"></i>
                                    </div>
                                    <div className="info-text">
                                        <h3>Mail Us</h3>
                                        <p>firoj.uddin2580@gmail.com</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-8 ">
                            <div className="contact-form">
                                <h2>We Love to Hear From You</h2>
                                <p>Please call or email contact form and we will be happy to assist you.</p>
                                <form onSubmit={sendEmail} className="main-contact-form">
                                    <div className="form-group">

                                        <input   required type="text" name="name"  className="form-control"
                                               placeholder="Your Name" />
                                        <input   required type="email" name="name"  className="form-control"
                                           placeholder="Your Email" />
                                        <input   required type="text" name="subject"  className="form-control"
                                                 placeholder="Subject" />
                                        <textarea   required name="message"  className="form-control"
                                                  rows="10" placeholder="Write Your Massage" />

                                        <button className="btn common-button" type="submit">SUBMIT NOW</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div id="map">
                <iframe
                    src="https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d4340.731660625645!2d90.41734496397146!3d23.81484035277416!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sen!2sbd!4v1573472709654!5m2!1sen!2sbd"
                    width="100%" height="700" frameBorder="0" style={{border: "0px"}} allowFullScreen="" />
            </div>
        </>
    );
};

export default ContactUs;
