import React from 'react';
import BorderImg from "../../BorderImg";
import "./upcomingEvent.css"
import upevent1 from "../../../img/upevent1.jpg"
import upevent2 from "../../../img/upevent2.jpg"
import upevent3 from "../../../img/upevent3.jpg"
import upevent4 from "../../../img/upevent4.jpg"
import {Link} from "react-router-dom";
const UpcomingEvent = () => {
    return (
        <>
            <div id="uc_event_section" className="section">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="heading">
                                <h3 className="subtitle">Our</h3>
                                <h2 className="title">Upcoming Event</h2>
                                <BorderImg />
                                <p className="text">
                                    There are many variations of passages of Lorem Ipsum available
                                    but the at majority have suffered alteration in some dummy.
                                </p>
                            </div>
                        </div>
                    </div>

                    <div className="section-content">
                        <div className="row">
                            <div className="col-md-3 col-sm-6">
                                <div className="up-coming-event">
                                    <img src={upevent1} alt="" className="img-fluid"/>
                                    <div className="event-text">
                                        <p className="name">business conference</p>
                                        <Link to="/">view details</Link>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-3 col-sm-6">
                                <div className="up-coming-event">
                                    <img src={upevent2} alt="" className="img-fluid"/>
                                    <div className="event-text">
                                        <p className="name">business conference</p>
                                        <Link to="/">view details</Link>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-3 col-sm-6">
                                <div className="up-coming-event">
                                    <img src={upevent3} alt="" className="img-fluid"/>
                                    <div className="event-text">
                                        <p className="name">business conference</p>
                                        <Link to="/">view details</Link>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-3 col-sm-6">
                                <div className="up-coming-event">
                                    <img src={upevent4} alt="" className="img-fluid"/>
                                    <div className="event-text">
                                        <p className="name">business conference</p>
                                        <Link to="/">view details</Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <Link to="/" className="btn action-btn more"
                                >More Event <i className="fas fa-arrow-right"></i
                                ></Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default UpcomingEvent;
