import React ,{useState} from 'react';
import BorderImg from "../../BorderImg";
import "./venuesection.css"
import { venues } from "../../../data";

const VenueSection = () => {
    const [venueS] = useState(venues);
    return (
        <>
            <div id="venue_section" className="section">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="heading">
                                <h3 className="subtitle">Conference</h3>
                                <h2 className="title">Popular Venues</h2>
                                <BorderImg/>
                                <p className="text">
                                    There are many variations of passages of Lorem Ipsum available
                                    but the at majority have suffered alteration in some dummy.
                                </p>
                            </div>
                        </div>
                    </div>

                    <div className="section-content">
                        <div className="row">
                            <div className="col-12">
                                <div className="venues owl-carousel owl-theme">
                                    {venueS.map((venue)=>(
                                        <div className="venue" key={venue.id}>
                                            <div className="venue-img">
                                                <img src={venue.venueImage} alt=""/>
                                                <div className="venue-place">{venue.Location}</div>
                                            </div>
                                            <div className="venue-details">
                                                <h3>{venue.venueName}</h3>
                                                <p className="text">
                                                    {venue.aboutVenue}
                                                </p>
                                            </div>
                                        </div>
                                    ))}


                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default VenueSection;
