import React,{useState} from 'react';
import Banner from "../../component/Banner/Banner";
import {BlogData,Users} from "../../blogData";
import {Link} from 'react-router-dom'
import Pagination from './Pagination'
const Blog = () => {
    const [users] = useState(Users);
    const [allPosts, setAllPoste] = useState(BlogData)
    const postPerPage = 9;
    const [currentPage, setCurrentPage] = useState(1);
    const numberOFPages = Math.ceil(allPosts.length / postPerPage)
    const indexOfLastPost = currentPage * postPerPage;
    const indexOfFirstPost = indexOfLastPost - postPerPage;
    const posts = allPosts.slice(indexOfFirstPost,indexOfLastPost)
    const current = (type) =>{
        if (type === 'prevb'){
            currentPage === 1 ? setCurrentPage(currentPage) : setCurrentPage(currentPage-1)
        }else if (type === 'nextb'){
            currentPage === numberOFPages ? setCurrentPage(currentPage) : setCurrentPage(currentPage+1)
        }
    }
    const setCurrentPageNumber = (number)=>{
        setCurrentPage(number)
    }
    return (
        <>
            <Banner title={"Blog"}/>


            <div id="blog_section" className="section">
                <div className="container">

                    <div className="blog-postes">
                        <div className="row">
                            {posts.map((blogdata)=>(
                                <div className="col-md-4 col-sm-6" key={blogdata.postId}>
                                    <Link to={`/${blogdata.postId}`} className="newses">
                                        <div className="news">
                                            <div className="news-img">
                                                <img src={blogdata.postImage} alt={blogdata.title} />
                                                <p className="news-publish green">{blogdata.publishedAt}</p>
                                            </div>
                                            <div className="news-details">
                                                <h3>{blogdata.title.slice(0,45)}</h3>
                                                <div className="details">
                                                    <p className="author">by {users.map((user)=>{
                                                        if (user.userId===blogdata.userId){
                                                            return  user.userName
                                                        }
                                                    })} </p>
                                                    <p>|</p>
                                                    <p className="tag"> Latest News</p>
                                                </div>
                                                <p className="text">{blogdata.postDetails.slice(0,160)}</p>
                                                <p className="mor">Read More</p>
                                            </div>
                                        </div>
                                    </Link>
                                </div>
                            ))}
                        </div>
                    </div>

                    <Pagination
                        numberOFPages={numberOFPages}
                        postPerPage={postPerPage}
                        currentPage={currentPage}
                        current = {current}
                        setCurrentPageNumber={setCurrentPageNumber}
                        setCurrentPage={setCurrentPage}
                    />
                </div>
            </div>
        </>
    );
};

export default Blog;
