import React from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import './App.css';
import Navbar from "./component/Navbar/Navbar";
import {Home, AboutUs,Event,Speaker, Blog, ContactUs, BecomeSponsor, BlogDetails,
    BlogRightSidebar, EventSingle, ImageGallery, Pricing} from "./pages";
import Footer from "./component/footer/Footer";



function App() {


  return (
    <>
        <Router>
            <Navbar/>
            <Switch>
                <Route exact path='/' component={Home}/>
                <Route exact path='/aboutus' component={AboutUs}/>
                <Route exact path='/event' component={Event}/>
                <Route exact path='/speaker' component={Speaker}/>
                <Route exact path='/blog_right_sidebar' component={BlogRightSidebar}/>
                <Route exact path='/blog' component={Blog}/>
                <Route exact path='/become_sponsor' component={BecomeSponsor}/>
                <Route exact path='/event_single' component={EventSingle}/>
                <Route exact path='/image_gallery' component={ImageGallery}/>
                <Route exact path='/pricing' component={Pricing}/>
                <Route exact path='/contactus' component={ContactUs}/>
                <Route exact path='/:id' component={BlogDetails}/>
                <Route  path=''/>
            </Switch>
            <Footer/>
        </Router>
    </>
  );
}

export default App;
