import React ,{useState}from 'react';
import BorderImg from "../../BorderImg";
import "./speakerSection.css"
import {speader} from "../../../data";
import {Link} from "react-router-dom";
const SpeakerSection = () => {
    const [speakers] = useState(speader);
    return (
        <>
            <div id="speaker_section" className="section">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="heading">
                                <h3 className="subtitle">Our</h3>
                                <h2 className="title">Awesome Speakers</h2>
                                <BorderImg />
                                <p className="text">
                                    There are many variations of passages of Lorem Ipsum available
                                    but the at majority have suffered alteration in some dummy.
                                </p>
                            </div>
                        </div>
                    </div>

                    <div className="section-content">
                        <div className="row">
                            <div className="col-12">
                                <div className="speakers row">
                                    {speakers.map((speaker)=>{
                                        return(
                                            <div className="col-lg-3 col-md-6" key={speaker.id}>
                                                <div className="speaker">
                                                    <div className="s-img">
                                                        <img src={speaker.thumbnailUrl} alt=""/>
                                                        <div className="s-social-link">
                                                            <div className="social-links">
                                                                <Link to="/" className="blue"
                                                                ><i className="fab fa-facebook-f"></i
                                                                ></Link>
                                                                <Link to="/" className="light-blue"
                                                                ><i className="fab fa-linkedin-in"></i
                                                                ></Link>
                                                                <Link to="/" className="cyan"
                                                                ><i className="fab fa-twitter"></i
                                                                ></Link>
                                                                <Link to="/" className="red"
                                                                ><i className="fab fa-google-plus-g"></i
                                                                ></Link>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="s-details">
                                                        <Link to="/">
                                                            <h5>{speaker.name}</h5>
                                                        </Link>
                                                        <p>{speaker.address}</p>
                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default SpeakerSection;
