import React from 'react';
import './purchaseNow.css'
import {Link} from "react-router-dom";
const PurchaseNow = () => {
    return (
        <>
            <div id="purchase_now" className="section">
                <div className="container">
                    <div className="row">
                        <div className="col text-center">
                            <h2 className="pn-text">
                                Our uses augmented reality to aid the visually impaired
                            </h2>
                            <Link to="/" className="btn action-btn">purchase now</Link>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default PurchaseNow;
