import React, {useState} from 'react';
import './sponsorSection.css'
import BorderImg from "../../BorderImg";
import {goldSponsor,silverSponsor} from "../../../data";

const SponserSection = () => {

    const [goldSopnsors, setGoldSopnsors] = useState(goldSponsor);
    const [silverSponsors, setSilverSponsors] = useState(silverSponsor);
    return (
        <>
            <div id="sponsor_section" className="section">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="heading">
                                <h3 className="subtitle">Our</h3>
                                <h2 className="title">Event Sponsor</h2>
                                <BorderImg />
                                <p className="text">
                                    There are many variations of passages of Lorem Ipsum available
                                    but the at majority have suffered alteration in some dummy.
                                </p>
                            </div>
                        </div>
                    </div>

                    <div className="section-content">
                        <div className="row">
                            <div className="col-12">
                                <div className="sponsor">
                                    <div className="gold-sponsor">
                                        <h3 className="text-center">Gold Sponsors</h3>

                                        <div className="row">
                                            {goldSopnsors.map((sponsor,index)=>(
                                                <div className="col-6 col-md" key={index}>
                                                    <img src={sponsor.imgUrl} alt={sponsor.name}/>
                                                </div>
                                            ))}

                                        </div>
                                    </div>
                                    <div className="silver-sponsor">
                                        <h3 className="text-center">Sliver Sponsors</h3>
                                        <div className="row">
                                            {silverSponsors.map((sponsor,index)=>(
                                                <div className="col-6 col-md" key={index}>
                                                    <img src={sponsor.imgUrl} alt={sponsor.name}/>
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default SponserSection;
