import React from 'react';

const SponsorForm = () => {

    const sponcerDetailsChange=(event)=>{

    }

    return (
        <>
            <div id="become_sponser" className="section">
                <div className="container">
                    <div className="row ">

                        <div className="col-12 ">
                            <div className="contact-form">
                                <p>There are many variations of passages of Lorem Ipsum available, but the majority have
                                    suffered alteration in some form, by a injected humour, or randomised words which
                                    don't look even slightly believable. If you are going to use a passage of Lorem to
                                    Ipsum, you need to be sure there isn't anything embarrassing hidden in the middle of
                                    text.</p>
                                <h2>Become a Sponsor</h2>
                                <form action="#" className="sponser-form">
                                    <div className="form-row">
                                        <div className="form-group col-md-6">

                                            <input type="text" name="name" className="form-control"
                                                   placeholder="First Name*" onChange={sponcerDetailsChange}/>
                                        </div>
                                        <div className="form-group col-md-6">

                                            <input type="email" name="name" className="form-control"
                                                   placeholder="Last Email*" onChange={sponcerDetailsChange} />
                                        </div>
                                        <div className="form-group col-md-6">

                                            <input type="text" name="name" className="form-control"
                                                   placeholder="Organization/Corporation*" onChange={sponcerDetailsChange} />
                                        </div>
                                        <div className="form-group col-md-6">

                                            <input type="email" name="name" className="form-control"
                                                   placeholder="Email*" onChange={sponcerDetailsChange} />
                                        </div>
                                        <div className="form-group col-md-12">
                                            <input type="text" name="name" className="form-control"
                                                   placeholder="Phone*" onChange={sponcerDetailsChange} />

                                        </div>
                                        <div className="form-group col-md-12">

                                            <textarea name="massage" className="form-control" rows="10"
                                                      placeholder="Massage*" onChange={sponcerDetailsChange} ></textarea>
                                            <button className="btn  action-btn" type="submit">SUBMIT NOW</button>
                                        </div>
                                    </div>


                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default SponsorForm;
