import React, {useState} from 'react';
import "./blogSection.css"
import BorderImg from "../../BorderImg";
import {Link} from "react-router-dom";

import {BlogData, Users} from "../../../blogData";
const BlogSection = () => {
    const [blogdatas, setBlogdata] = useState(BlogData);
    const [users, setUsers] = useState(Users);

    return (
        <>
            <div id="blog_section" className="section">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="heading">
                                <h3 className="subtitle">Our</h3>
                                <h2 className="title">Latest News</h2>
                                <BorderImg />
                                <p className="text">
                                    There are many variations of passages of Lorem Ipsum available
                                    but the at majority have suffered alteration in some dummy.
                                </p>
                            </div>
                        </div>
                    </div>

                    <div className="section-content">
                        <div className="row">
                            {blogdatas.map((blogdata,index)=> {
                                if (index<3){
                                    return(
                                    <div className="col-md-4 col-sm-6" key={blogdata.postId}>
                                        <Link to={`/${blogdata.postId}`} className="newses">
                                            <div className="news">
                                                <div className="news-img">
                                                    <img src={blogdata.postImage} alt={blogdata.title}/>
                                                    <p className="news-publish green">{blogdata.publishedAt}</p>
                                                </div>
                                                <div className="news-details">
                                                    <h3>{blogdata.title.slice(0, 45)}</h3>
                                                    <div className="details">
                                                        <p className="author">by {users.map((user) => {
                                                            if (user.userId === blogdata.userId) {
                                                                return user.userName
                                                            }
                                                        })} </p>
                                                        <p>|</p>
                                                        <p className="tag"> Latest News</p>
                                                    </div>
                                                    <p className="text">{blogdata.postDetails.slice(0, 160)}</p>
                                                    <p className="mor">Read More</p>
                                                </div>
                                            </div>
                                        </Link>
                                    </div>
                                    )
                                }

                            })}
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default BlogSection;
