import React,{useState} from 'react';
import BorderImg from "../../BorderImg";
import {Imagegalary} from "../../../data";

const ImageGallerySection = () => {
    const [imagesdata] = useState(Imagegalary);
    return (
        <>
            <div id="gallery_section" className="section">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-12">
                            <div className="heading">
                                <h3 className="subtitle">Our Conference</h3>
                                <h2 className="title">See Photo Gallery</h2>
                                <BorderImg/>
                                <p className="text">There are many variations of passages of Lorem Ipsum available but
                                    the at
                                    majority have suffered alteration in some dummy.</p>
                            </div>
                        </div>
                    </div>
                    <div className="section-content">
                        <div className="galleries ">
                            <div uk-lightbox="animation: slide" className="row ">
                                {imagesdata.map((imgData,index)=>(
                                    <figure className="col-sm-6 col-md-4 col-lg-3" key={imgData.id}>
                                        <a href={imgData.url} data-size="1600x1067" className="uk-inline">
                                            <img src={imgData.url} alt={imgData.title} className="img-fluid"/>
                                        </a>
                                    </figure>
                                ))}

                            </div>

                        </div>


                    </div>
                </div>
            </div>
        </>
    );
};

export default ImageGallerySection;
