        "title":"Hurricane Teddy's eye looks like a giant stadium",
        "postImage":"https://mondrian.mashable.com/uploads%252Fcard%252Fimage%252F1503211%252Fafda65c0-2b46-4b7b-89e9-f5a5a5a5cb62.webp%252F950x534__filters%253Aquality%252880%2529.webp?signature=-ucCbfaAWAhY5fFF63btM03pxvI=&source=https%3A%2F%2Fblueprint-api-production.s3.amazonaws.com",
        "postDetails":'Storm investigators flew through Hurricane Teddy on Thursday, capturing impressive imagery of the cyclone\'s stadium-like eye.\n' +
            '\n' +
            'A big, clear eye is indicative of a powerful hurricane (on Friday morning, Teddy packed 130 mph winds). At the storm\'s core, violent thunderstorms, feeding on warm moist air, rise up as they rotate around the center of the cyclone, which can create a circular wall of clouds. Inside exists a calm, relatively clear eye.  \n' +
            '\n' +
            'Flights in and around Teddy began Wednesday, as National Oceanic and Atmospheric Administration (NOAA) researchers measured the storm\'s wind speed and took other observations. The hurricane — forecast to hit somewhere in the Nova Scotia region next week — is one of a record-breaking number of tropical storms that have formed by this date in the Atlantic this year. \n' +
            '\n' +
            '2020 is currently on pace to break 2005\'s total record for most named storms in a year (tropical storms, which are organized cyclones with winds speeds of at least 39 mph, earn a name.) So far, 21 named storms have formed this year, stoked by warm ocean temperatures and diminished hurricane-shredding winds. This storm season paces well ahead of 2005 when the 21st named storm didn\'t form until Oct.8. This means scientists have now exhausted the 21 storm names designated for 2020, so they\'ll use Greek letters like Alpha, Beta, and Gamma for this year\'s remaining cyclones. It\'s been an exceptionally active year. \n' +
            '\n' +
            '"The number of storms is extraordinary," Falko Judt, a research meteorologist at the National Center for Atmospheric Research, told Mashable on Monday as five cyclones spun in the Atlantic.',
