        "title":"Netflix drops creepy trailer for post-apocalyptic horror film 'Cadaver', just in time for Halloween",
        "postImage":"https://firojuddina.github.io/brave-sty/img/news/02.jpg",
        "postDetails":'There are lots of of post-apocalyptic films out there and plenty of creepy mask-wearing cult films, but the two sub-genres of horror are very rarely spliced together. \n' +
            '\n' +
            'Cadaver – the first Norwegian Netflix Original film – looks set to change that. In the trailer above, a struggling family are lured to a large hotel with the promise of "a small distraction from this horrible everyday life." But the distraction – which involves everybody wearing Purge-style masks and wandering through dark, eerie rooms – looks likely to be every bit as scary as the world outside.',
        "postTag":['CADAVER','ENTERTAINMENT', 'NETFLIX', 'TRAILERS'],
        'catagoryId':1,
        "publishedAt":'21.2.2021',
        "like":123,
