        "title":"The Understated Significance of Giannis' Second MVP Award",
        "postImage":"https://blogstorage.s3.amazonaws.com/uploads/pinax-images/image-set-99116/8542546c-b244-445f-afc3-6cb30216587b.jpg",
        "postDetails":'To the ire of fans, many who thought that Lebron James was robbed of being named as the league’s Most Valuable Player for a fifth time, the Greek Freak received 85 out of 101 first-place votes. James received the other 16 as no other player received a single one.\n' +
            '\n' +
            'Giannis now becomes the 12th player in NBA history to win back-to-back MVP’s joining legends like Bill Russell, Magic Johnson and Michael Jordan, and current day superstars Stephen Curry and Lebron James on the prestigious list.\n' +
            '\n' +
            'The MVP also becomes the third player in history to have won both the Most Valuable Player and Defensive Player of the Year awards in the same season. He joins ‘His Airness’ Michael Jordan, who won both in 1987/88, and Hakeem ‘The Dream’ Olajuwon, the awards’ recipient in 1993/94, as the only players to have done so.',
