import React,{useState} from 'react';
import './comment.css'
import {Comments} from "../../blogData";
import avater from '../../img/speaker/sk1.jpg'

const Comment = ({postId}) => {
    const gComment = Comments;
    console.log(gComment.length);
    const [postComment, setPostComment] = useState({
        "postId": postId,
        "id":gComment.length+1,
        "name": "",
        "email": "",
        "body": ""
    });

    const commentInout=(event)=>{

    }
    return (
        <>
            <form  className="comment-form">
                <div className="form-row">
                    <div className="form-group col-md-6">
                        <input type="text" name="name"  className="form-control"
                               placeholder="Your Name" onChange={commentInout} />
                    </div>
                    <div className="form-group col-md-6">
                        <input type="email" name="email"  className="form-control"
                               placeholder="Your Email" onChange={commentInout} />
                    </div>
                </div>
                <div className="form-group">
                    <textarea name="massage"  className="form-control" rows="5"
                              placeholder="Write your comment"  onChange={commentInout} >{postComment.body}</textarea>

                    <button className="btn common-button" type="submit">SUBMIT</button>
                </div>
            </form>
            {
                gComment.map((comment,index)=>{
                    if (comment.postId == postId){
                        return (
                            <div className="comment_body my-4" key={comment.id}>
                                <div className="row">
                                    <div className="col-2">
                                        <div className="avater">

                                            <img src={avater} alt="" className="avater_img img-fluid" />
                                        </div>
                                    </div>
                                    <div className="col-10">
                                        <h4 className="comment_user">{comment.name.slice(0,25)}</h4>
                                        <p className="lead">{comment.body}</p>
                                    </div>
                                </div>
                            </div>
                        )
                    }
                })
            }
        </>
    );
};

export default Comment;
