import React,{useState} from 'react';
import './navbar.css'
import {Link, NavLink} from "react-router-dom";
import LoginRegister from "./LoginRegister";
const Navbar = () => {

    const [showDropdown, setShowDropdown] = useState(false);
    const [click, setClick] = useState(true);

    const handleClick = () => setClick(!click);
    const closeMobileMenu = () => setClick(false);
    const onMouseEnter = () => {
        if (window.innerWidth < 960) {
            setShowDropdown(false);
        } else {
            setShowDropdown(true);
        }
    };
    const onMouseLeave = () => {
        if (window.innerWidth < 960) {
            setShowDropdown(false);
        } else {
            setShowDropdown(false);
        }
    };
    const onClick = () => {
        setShowDropdown(!showDropdown);

    };
    const [sctoled, setScroled] = useState(false);

    window.addEventListener('scroll', () => {
        const scrollPos = window.scrollY;
        if (scrollPos > 10) {
            setScroled(true)
        } else {
            setScroled(false)
        }
    });

    return (
        <>
            <header id="header">
                <nav className={`navbar navbar-expand-lg py-0 fixed-top ${sctoled && "scrolled"}` } >
                    <div className="container">
                        <Link className="navbar-brand" to="./" onClick={closeMobileMenu}>
                            <span className="first-brand">Brave</span>
                            <span className="last-brand">STY</span>
                        </Link>
                        <button className="navbar-toggler d-lg-none" onClick={handleClick}>
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className={click ? 'collapse navbar-collapse' : 'navbar-collapse'} >
                            <ul className="navbar-nav ml-auto ">

                                <li className="nav-item">
                                    <NavLink  className="nav-link" exact to="/">home</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" exact to="/aboutus"  onClick={closeMobileMenu}>about us</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" exact to='/event'  onClick={closeMobileMenu}>event</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" exact to='/speaker'  onClick={closeMobileMenu}>Speaker</NavLink>
                                </li>
                                <li className="nav-item navbar-dropdown"
                                    onMouseEnter={onMouseEnter}
                                    onMouseLeave={onMouseLeave}
                                    onClick={onClick}
                                >
                                    <a href="#!" className="nav-link dropdown-toggle"  onClick={closeMobileMenu}>Pages</a>
                                    {showDropdown && (
                                        <div className="nav-dropdown">
                                            <NavLink className="dropdown-item" exact to="/pricing"  onClick={closeMobileMenu}>pricing</NavLink>
                                            <NavLink className="dropdown-item" exact to="/image_gallery"  onClick={closeMobileMenu}>image gallery</NavLink>
                                            <NavLink className="dropdown-item" exact to="/become_sponsor"  onClick={closeMobileMenu}>become sponsor</NavLink>
                                        </div>
                                    )}

                                </li>
                                <li className="nav-item ">
                                    <NavLink className="nav-link" exact to="/blog"  onClick={closeMobileMenu}>blog</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" exact to="/contactus"  onClick={closeMobileMenu}>contact us</NavLink>
                                </li>


                            </ul>

                        </div>
                        <LoginRegister />
                    </div>
                </nav>
            </header>
        </>
    );
};

export default Navbar;
