import React , {useState} from 'react';
import {Link} from "react-router-dom";
import './login.css'
const LoginButton = (props) => {
    const {
        email,
        password,
        setEmail,
        setPassword,
        handleLogin,
        handleSignup,
        hasAccount,
        setHasAccount,
        emailError,
        passwordError,
        handleGoogleLogin
    } = props
    const [modal, setModal] = useState(false);
    const showLoginRegisterModal=()=>{
        setModal(true)
    }
    const closeModal=()=>{
        setModal(false)
    }

    return (
        <>
            <div className="d-lg-block d-none">
                <Link to="/" className="btn action-btn" onClick={showLoginRegisterModal}>Login</Link>
            </div>
            {modal && (
            <div className="modal">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">

                        <div className="modal-header">
                            <h3 className="modal-title" id="exampleModalLabel">User Login</h3>
                            <button
                                type="button"
                                className="close "
                                onClick={closeModal}

                            >
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">

                            <section id={"login"} className={"login"}>
                                <div className="loginContainer">
                                    <button onClick={handleGoogleLogin}>login With Google</button>
                                    <label>User Email</label>
                                    <input type="email"
                                           required
                                           autoFocus
                                           value={email}
                                           onChange={(e)=>setEmail(e.target.value)}
                                    />
                                    <p className="errorMsg">{emailError}</p>

                                    <label>User Password</label>
                                    <input type="password"
                                           required
                                           autoFocus
                                           value={password}
                                           onChange={(e)=>setPassword(e.target.value)}
                                    />
                                    <p className="errorMsg">{passwordError}</p>

                                    <div className="btnContainer">
                                        {hasAccount ? (
                                            <>
                                                <button onClick={handleLogin}>Sign in</button>
                                                <p>Dont Have an account ? <span onClick={()=>setHasAccount(!hasAccount)}>sign up</span></p>
                                            </>
                                        ):(
                                            <>
                                                <button onClick={handleSignup}>Sign up</button>
                                                <p>Already have an account. <span onClick={()=>setHasAccount(!hasAccount)}>sign in</span></p>
                                            </>
                                        )}
                                    </div>
                                </div>

                            </section>
                        </div>
                        <div className="modal-footer">
                            <button
                                type="button"
                                className="btn btn-secondary"
                                onClick={closeModal}
                            >
                                Close
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            )}
        </>
    );
};

export default LoginButton;
