import React from 'react';
import "./homebanner.css"
import {Link} from "react-router-dom";
const HomeBanner = () => {
    return (
        <>
            <div id="home_banner">
                <div id="carouselId" className="carousel slide" data-ride="carousel">
                    <ol className="carousel-indicators d-none">
                        <li data-target="#carouselId" data-slide-to="0" className="active"> </li>
                        <li data-target="#carouselId" data-slide-to="1"> </li>
                        <li data-target="#carouselId" data-slide-to="2"> </li>
                    </ol>
                    <div className="carousel-inner " role="listbox">
                        <div className="carousel-item active">
                            <div className="carousel-caption d-none d-md-block">
                                <h3 className="slider-subtitle">a unique</h3>
                                <h2 className="slider-title">business convention</h2>
                                <p className="slider-text">There are many variations of passages of Lorem Ipsum
                                    available, but the
                                    majority have suffered alteration in
                                    some form by injected humour or randomised words.</p>

                                <Link to="#" className="btn action-btn buy">buy tickets</Link>
                                <Link to="#" className="btn action-btn download">download pdf</Link>
                            </div>
                        </div>
                        <div className="carousel-item">
                            <div className="carousel-caption d-none d-md-block">
                                <h3 className="slider-subtitle">a unique</h3>
                                <h2 className="slider-title">business convention</h2>
                                <p className="slider-text">There are many variations of passages of Lorem Ipsum
                                    available, but the
                                    majority have suffered alteration in
                                    some form by injected humour or randomised words.</p>

                                <Link to="#" className="btn action-btn buy">buy tickets</Link>
                                <Link to="#" className="btn action-btn download">download pdf</Link>
                            </div>
                        </div>
                        <div className="carousel-item">
                            <div className="carousel-caption d-none d-md-block">
                                <h3 className="slider-subtitle">a unique</h3>
                                <h2 className="slider-title">business convention</h2>
                                <p className="slider-text">There are many variations of passages of Lorem Ipsum
                                    available, but the
                                    majority have suffered alteration in
                                    some form by injected humour or randomised words.</p>

                                <Link to="#" className="btn action-btn buy">buy tickets</Link>
                                <Link to="#" className="btn action-btn download">download pdf</Link>
                            </div>
                        </div>
                    </div>
                    <a className="carousel-control-prev" href="#carouselId" role="button" data-slide="prev">
                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                        <span className="sr-only">Previous</span>
                    </a>
                    <a className="carousel-control-next" href="#carouselId" role="button" data-slide="next">
                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                        <span className="sr-only">Next</span>
                    </a>
                </div>
            </div>
        </>
    );
};

export default HomeBanner;
