import React from 'react';
import {Link} from "react-router-dom";

const PostShare = ({blogdata}) => {


    const pinterestImg = document.querySelector(".pinterest-img");
    let postUrl = encodeURI(document.location.href);
    let postTitle = encodeURI("Hi everyone, please check this out: ");
    let postImg = encodeURI(blogdata.postImage);

    const facebookBtn = `https://www.facebook.com/sharer.php?u=${postUrl}`;
    const twitterBtn = `https://twitter.com/share?url=${postUrl}&text=${postTitle}`;
    const pinterestBtn = `https://pinterest.com/pin/create/bookmarklet/?media=${postImg}&url=${postUrl}&description=${postTitle}`;
    const linkedinBtn = `https://www.linkedin.com/shareArticle?url=${postUrl}&title=${postTitle}`;
    const whatsappBtn = `https://wa.me/?text=${postTitle} ${postUrl}`;


    return (
        <>
            <div className="post-share">
                <h4>Share:</h4>
                <a href={facebookBtn} className="post-share"><i className="fab fa-facebook-f"></i></a>
                <a href={twitterBtn} className="post-share"><i className="fab fa-twitter"></i></a>
                <a href={pinterestBtn} className="post-share"><i className="fab fa-pinterest"></i></a>
                <a href={linkedinBtn} className="post-share"><i className="fab fa-linkedin-in"></i></a>
                <a href={whatsappBtn} className="post-share"><i className="fab fa-whatsapp"></i></a>
            </div>
        </>
    );
};

export default PostShare;
