import React , {useState,useEffect} from 'react';
import './pagination.css'
const Pagination = ({numberOFPages,currentPage,current,setCurrentPage,setCurrentPageNumber}) => {
    const pageNumber = [];
    for (let i=1;i<= numberOFPages; i++){
        pageNumber.push(i);
    }


    const [arrOfPageNumber,setArrOfPageNumber ] = useState([]);
    useEffect(() => {
       let dotInitial ='...';
       let dotLeft ='... ';
        let dotRight =' ...';
        let tempNumberOfpage = [...arrOfPageNumber];
        if (numberOFPages < 6 ){
            tempNumberOfpage = pageNumber
        }else if (currentPage >=1 && currentPage <=3){
            tempNumberOfpage = [1,2,3,4,dotInitial,numberOFPages]
        }else if (currentPage ===4){
            const sliced = pageNumber.slice(0,5)
            tempNumberOfpage = [...sliced,dotInitial,numberOFPages]
        }else if (currentPage >4 && currentPage <numberOFPages-2){
            const sliced1 = pageNumber.slice(currentPage-2,currentPage)
            const sliced2 = pageNumber.slice(currentPage,currentPage+1)
            tempNumberOfpage = [1,dotLeft,...sliced1,...sliced2,dotRight,numberOFPages]
        }else if (currentPage >numberOFPages-3){
            const sliced = pageNumber.slice(currentPage-4)
            tempNumberOfpage = [1,dotLeft,...sliced]
        }else if (currentPage === dotInitial){
            setCurrentPage(arrOfPageNumber[arrOfPageNumber.length-3]+1)
        }else if (currentPage === dotLeft){
            setCurrentPage(arrOfPageNumber[4] - 2)
        }else if (currentPage === dotRight){
            setCurrentPage(arrOfPageNumber[4] + 2)
        }
        setArrOfPageNumber(tempNumberOfpage)
    }, [currentPage]);

    return (
        <>
            <div className="p-pagination ">
                <nav aria-label="Page navigation d-flex">
                    <ul className="pagination justify-content-center">
                        <li className={`page-item ${currentPage===1 && 'disabled'}`}>
                            <a
                                onClick={()=>current("prevb")} className="page-link" href="#!" aria-label="Previous">
                                <span aria-hidden="true">Previus</span>
                            </a>
                        </li>
                        {//arrOfP//
                            pageNumber.map((number,index)=>(
                            <li className={`page-item ${currentPage === number && 'p-active'}`} key={index}>
                                <a
                                    className={`page-link` }
                                    href="#!"
                                    onClick={()=>setCurrentPageNumber(number)}
                                >{number}</a>
                            </li>

                        ))}
                        <li className={`page-item ${currentPage===numberOFPages && 'disabled'}`}>
                            <a
                                onClick={()=>current("nextb")}
                                className="page-link" href="#!" aria-label="Next">
                                <span aria-hidden="true">Next</span>
                            </a>
                        </li>
                    </ul>
                </nav>
            </div>
        </>
    );
};

export default Pagination;
