import React,{useState} from 'react';
import BorderImg from "../../BorderImg";
import "./clientSection.css";
import {clientReview} from "../../../data";
import {Link} from "react-router-dom";

const ClientSection = () => {

    const [clientReviews, setClientReviews] = useState(clientReview);
    return (
        <>
            <div id="clients_section" className="section">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="heading">
                                <h3 className="subtitle">Our</h3>
                                <h2 className="title">Staisfide Clients</h2>
                                <BorderImg />
                                <p className="text">
                                    There are many variations of passages of Lorem Ipsum available
                                    but the at majority have suffered alteration in some dummy.
                                </p>
                            </div>
                        </div>
                    </div>

                    <div className="section-content">
                        <div className="row">
                            <div className="col-12">
                                <div className="testimonial owl-carousel owl-theme">
                                    {clientReviews.map((clientReview)=>(
                                        <div className="client" key={clientReview.id}>
                                            <div className="client-review">
                                                <i className="fas fa-quote-right text-danger"></i>
                                                <p className="text">
                                                    {clientReview.feedback}
                                                </p>
                                            </div>
                                            <div className="client-img">
                                                <img src={clientReview.avatar} alt={clientReview.name}/>
                                                <div className="client-details">
                                                    <Link to="/" className="client-name">
                                                        <h4>{clientReview.name}</h4>
                                                    </Link>
                                                    <p className="desig">{clientReview.occopation}</p>
                                                </div>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default ClientSection;
